<template>
  <div class="alluser">
      <el-card>
        <el-row>
          <el-col :span="10">
            <el-input v-model="myparams.keyword" placeholder="请输入内容" clearable @change="search"></el-input>
          </el-col>
          <el-col :span="2">
            <el-button type="primary" plain @click="search">主要按钮</el-button>
          </el-col>
        </el-row>
      </el-card>
      <div class="home-main">
        <el-table
          :data="userList"
          stripe
          border
          status-icon
          style="width: 100%">
            <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            label="编号"
            type="index"
            >
          </el-table-column>
          <el-table-column
            prop="nickname"
            label="姓名"
            width="100">
          </el-table-column>
          <el-table-column
            label="日期"
            width="180">
            <template slot-scope="scope">
              <i class="el-icon-time"></i>
              <span style="margin-left: 10px">{{ formatTime(scope.row.posttime)}}</span>
            </template>
          </el-table-column>
          <el-table-column
            width="150"
            prop="contact"
            label="电话">
          </el-table-column>
          <el-table-column
            prop="weixin"
            label="微信"
            width="180">
          </el-table-column>
          <el-table-column
            width="150"
            prop="qq"
            label="QQ">
          </el-table-column>

          <el-table-column
            width="180"
            prop="zhuanye"
            label="专业">
            <template slot-scope="scope" >
              <div class="major">
                {{ scope.row.zhuanye }}
                <span class="major" style="margin-left: 10px">{{ scope.row.infoclass }}</span>
              </div>
            </template>
          </el-table-column>
         <el-table-column
            width="150"
            prop="laiyuan"
            label="信息来源">
          </el-table-column>
          <el-table-column
            prop="dizhi"
            label="地区">
          </el-table-column>
          <el-table-column
            width="100"
            prop="kefu"
            label="客服">
          </el-table-column>
          <el-table-column
            width="150"
            label="操作">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="编辑" placement="top">
                <el-button type="primary" icon="el-icon-edit" size="small" @click="clickChange(scope.row.id,scope.row)"></el-button>
              </el-tooltip>
                <el-tooltip class="item" effect="dark" content="删除" placement="top">
                  <el-button type="danger" icon="el-icon-delete" size="small" @click="delUser(scope.row.id)"></el-button>
                </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <div class="pages">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[20, 50, 60]"
            :current-page="myparams.page"
            :page-size="myparams.num"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
      <ChangeDialog ref="changDialogInfo"/>
  </div>
</template>

<script>
import { getallUser, delUserRow } from '@/network/networds'
import { dateFormatFun } from '@/assets/js/common'
import ChangeDialog from './children/ChangeDialog'
export default {
  components: {
    ChangeDialog
  },
  data () {
    return {
      userList: [],
      total: 0,
      myparams: {
        num: 20,
        page: 1,
        keyword: ''
      }
    }
  },
  mounted () {
    this.getuserList()
  },
  methods: {
    // 弹框修改用户
    clickChange (id, data) {
      this.$refs.changDialogInfo.dialogTableVisible = true
      this.$refs.changDialogInfo.rowId = id
      this.$refs.changDialogInfo.getSingleUser()
      window.sessionStorage.setItem('singleUser', JSON.stringify(data))
      //
    },
    // 删除用户
    delUser (id) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delUserRow(id)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        this.getuserList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 获取用户
    getuserList () {
      getallUser(this.myparams).then(res => {
        this.userList = res.data.results
        this.total = res.data.num
      })
    },
    // 格式化时间戳
    formatTime (time) {
      return dateFormatFun(time, 2)
    },
    // 改变每页显示条数
    handleSizeChange (e) {
      this.myparams.num = e
      this.getuserList()
    },
    // 改变当前页
    handleCurrentChange (e) {
      this.myparams.page = e
      this.getuserList()
    },
    // 查询
    search () {
      this.getuserList()
    }
  }
}
</script>

<style lang="less" scope>
  .home-main{
    margin-top:30px;
  }
  .major{
    position:relative;
    span{
      position: absolute;
      right: 0;
      top: -6;
      color:#409EFF;
      font-size: 12px;
      transform: scale(0.9);
    }
  }
  .pages{
    height: 30px;
    margin-top:20px;
    text-align: center;
    align-items: center;

  }
</style>
