<template>
    <el-dialog title="修改客户信息" width="70%" :visible.sync="dialogTableVisible" :close-on-click-modal="false" @close="closeDialog">
        {{rowId}}
        <el-form :model="adduserForm" :rules="adduserFormRules" ref="adduserForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="客户名称" prop="nickname">
            <el-input v-model.trim="adduserForm.nickname"></el-input>
        </el-form-item>
        <el-form-item label="联系电话" prop="contact">
            <el-input v-model.trim="adduserForm.contact"></el-input>
        </el-form-item>
        <el-form-item label="QQ" prop="qq">
            <el-input v-model.trim="adduserForm.qq"></el-input>
        </el-form-item>
        <el-form-item label="微信" prop="weixin">
            <el-input v-model.trim="adduserForm.weixin"></el-input>
        </el-form-item>
        <el-form-item label="专业咨询" prop="zhuanye">
            <el-row>
            <el-col :span="4">
                <el-select v-model="adduserForm.zhuanye" placeholder="请选择">
                <el-option
                    :value="item.classname"
                    v-for="item in zhuanyeList"
                    :key="item.id">
                </el-option>
                </el-select>
            </el-col>
            <el-col :span="20">
                <el-input v-model="adduserForm.infoclass"></el-input>
            </el-col>
            </el-row>
        </el-form-item>
        <el-form-item label="信息来源" prop="laiyuan">
            <el-input v-model.trim="adduserForm.laiyuan"></el-input>
        </el-form-item>
        <el-form-item label="所在地" prop="dizhi">
            <el-input v-model.trim="adduserForm.dizhi"></el-input>
        </el-form-item>
        <el-form-item label="选择客服" prop="kefu">
            <el-select v-model.trim="adduserForm.kefu" multiple placeholder="请选择">
            <el-option
                :value="item.classname"
                v-for="item in kefuList"
                :key="item.id">
            </el-option>
            </el-select>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="updateUser">修改</el-button>
            <el-button @click="resetForm(adduserForm)">重置</el-button>
        </el-form-item>
        </el-form>
    </el-dialog>
</template>

<script>
import { xialaInfo, getRowUser, updateRowUser } from '@/network/networds'
import { validatorRule } from '@/assets/js/validate'
export default {
  data () {
    return {
      dialogTableVisible: false,
      rowId: 0,
      kefuList: [],
      zhuanyeList: [],
      adduserForm: {
        nickname: '',
        contact: '',
        qq: '',
        weixin: '',
        laiyuan: '',
        dizhi: '',
        kefu: [],
        zhuanye: '',
        infoclass: ''
      },
      adduserFormRules: {
        nickname: [
          { required: true, message: '请输入活动名称', trigger: 'blur' }
        ],
        zhuanye: [
          { required: true, message: '请输入专业', trigger: 'blur' }
        ],
        laiyuan: [
          { required: true, message: '请输入来源', trigger: 'blur' }
        ],
        dizhi: [
          { required: true, message: '请输入地址', trigger: 'blur' }
        ],
        kefu: [
          { type: 'array', required: true, message: '请至少选择一个客服', trigger: 'change' }
        ],
        infoclass: [
          { required: true, message: '请输入专业信息', trigger: 'blur' }
        ],
        contact: [
          { validator: validatorRule, trigger: 'blur' }
        ],
        qq: [
          { validator: validatorRule, trigger: 'blur' }
        ],
        weixin: [
          { validator: validatorRule, trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    this.getXialaInfo()
    const singleUser = window.sessionStorage.getItem('singleUser')
    if (singleUser) {
      window.sessionStorage.removeItem('singleUser')
    }
  },
  watch: {
    // rowId(){
    //     this.getSingleUser()
    // }
  },
  methods: {
    // 关闭对话框
    closeDialog () {
      window.sessionStorage.removeItem('singleUser')
    },
    // 获取用户数据
    getSingleUser (id) {
      getRowUser(this.rowId).then(res => {
        this.adduserForm = res.data.results
        this.adduserForm.kefu = this.adduserForm.kefu.split('-')
      })
    },
    // 更新客户信息
    updateUser () {
      this.$refs.adduserForm.validate(valid => {
        if (valid) {
          updateRowUser(this.adduserForm).then(res => {
            this.$message.success('更新成功')
            this.dialogTableVisible = false
            this.$parent.getuserList()
          })
        }
      })
    },
    // 重置表单
    resetForm () {
      this.$refs.adduserForm.resetFields()
      this.getSingleUser()
    },
    // 获取客服和专业咨询
    getXialaInfo () {
      xialaInfo().then(res => {
        this.zhuanyeList = res.data.infoclass
        this.kefuList = res.data.kefu
      })
    }

  }
}
</script>

